@import "variables";

.header__fixedNav {
  display: none;
  width: 86%;
  position: fixed;
  top: 0;
  left: 50%;
  margin-left: -43%;
  z-index: 10;
  height: 50px;
  background-color: @mainColor;
  ul {
    display: flex;
    background-color: @fontColor;
    li {
      &:nth-child(1) {
        width: 20%;
        background-color: #77c9cc;
        a {
          display: block;
          font: 1.125em/50px @sansbold;
          color: @linkColor;
          text-decoration: none;
          i {
            padding: 0 15px 0 25px;
          }
          &:hover {
            color: #fff;
            background-color: @hoverColor;
            transition: .5s;
          }
        }
      }
      &:nth-child(2) {
        width: 55%;
        text-align: center;
        input {
          width: 90%;
          height: 35px;
          padding-left: 25px;
          font: 1em/50px @sansregular;
          color: #9e9e9e;
          border: 3px solid @fontColor;
          border-radius: 50px;
          outline: none;
        }
        i {
          font-size: 1.2em;
          line-height: 45px;
          position: relative;
          left: -50px;
          top: 2px;
          color: @mainColor;
        }
      }
      &:nth-child(3) {
        width: 17%;
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          font: 1.125em/50px @sansregular;
          color: #fff;
          text-decoration: none;
          i {
            padding: 5px;
            font-size: .875em;
            border: 2px solid #fff;
            border-radius: 50px;
          }
        }
      }
      &:nth-child(4) {
        width: 3%;
        a {
          display: block;
          i {
            width: 50px;
            font-size: 1.5em;
            line-height: 50px;
            text-align: center;
            color: #fff;
            background-color: #7d3daf;
          }
        }
      }
      &:nth-child(5) {
        width: 15%;
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          font: 1.125em/50px @sansregular;
          color: #fff;
          text-decoration: none;
        }
      }
    }
  }
}
