@import "variables";

.authorization {
  padding-bottom: 25px;
  width: 390px;
  min-height: 450px;
  border-radius: 5px;
  background: #fff;
  position: fixed;
  top: 45%;
  left: 50%;
  margin-top: -130px;
  margin-left: -195px;
  display: none;
  opacity: 0;
  z-index: 20;
  .typeOfAuthor {
    margin-bottom: 30px;
    ul{
      display: flex;
      margin-bottom: 40px;
      li{
        width: 50%;
        a {
          display: inline-block;
          width: 100%;
          height: 65px;
          font: 1.125em/65px @sansbold;
          color: #98a2a4;
          text-align: center;
          text-decoration: none;
          background-color: #ebebeb;
          border-radius: 5px;
          &:hover {
            outline: none;
          }
        }
      }
    }
  }
  .author,
  .regist{
    text-align: center;
    p {
      position: relative;
      margin: 10px 0;
      .user,
      .shield,
      .phone,
      .envelope{
        position: absolute;
        top: 15px;
        left: 85px;
        color: @mainColor;
      }
      input{
        width: 200px;
        height: 40px;
        padding-left: 50px;
        font: 1em/40px @sansregular;
        color: #828282;
        border: 1px solid #e6e6e6;
        border-radius: 5px;
        outline: none
      }
      select{
        width: 250px;
        height: 40px;
        padding-left: 25px;
        font: 1em/40px @sansregular;
        color: #828282;
        border: 1px solid #e6e6e6;
        border-radius: 5px;
        outline: none
      }
      .asterisk {
        position: absolute;
        top: 20px;
        right: 85px;
        font-size: .35em;
        color: #ff0000;
      }
      label {
        .checkbox {
          display: none;
        }
        .checkbox-custom {
          position: relative;
          width: 20px;
          height: 20px;
          border: 1px solid #e6e6e6;
          border-radius: 5px;
        }
        .checkbox-custom,
        .label {
          display: inline-block;
          vertical-align: middle;
        }
        .checkbox:checked + .checkbox-custom:before {
          content: "";
          display: block;
          position: absolute;
          top: 2px;
          right: 2px;
          bottom: 2px;
          left: 2px;
          background-color: @mainColor;
          border-radius: 5px;
        }
      }
    }
    button {
      margin: 30px 0;
      width: 250px;
      height: 40px;
      font: 1em/1.125em @sansregular;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      background-color: @mainColor;
      border: 1px solid @mainColor;
      border-radius: 5px;
      outline: none;
      &:hover {
        cursor: pointer;
      }
    }
    a {
      display: block;
      font: .875em/1em @sansregular;
      color: #828282;
    }
  }
  .ui-tabs-active {
    .ui-tabs-anchor {
      color: @mainColor !important;
      background-color: #fff !important;
      outline: none;
    }
  }
  .socialAuthor {
    text-align: center;
    p {
      margin: 25px 0;
      font: 1em/1.125em @sansbold;
      color: @mainColor;
    }
    ul {
      display: flex;
      justify-content: center;
      li {
        a {
          margin: 0 10px;
          display: inline-block;
          width: 140px;
          height: 40px;
          font: 1em/40px @sansregular;
          color: #fff;
          text-align: center;
          text-decoration: none;
          border-radius: 5px;
        }
        .facebook {
          background-color: #3b5998;
          border-bottom: 1px solid #293e6a;
        }
        .gPlus {
          background-color: #db4437;
          border-bottom: 1px solid #8a251d;
        }
      }
    }
  }
}